.widget {
  margin-bottom: 1.2em;
}

.widget ul {
  padding-left: 2em;
}

.widget select {
  min-width: 150px;
}

.comment-form label {
  display: block;
  padding-bottom: 4px;
}

.comment-form textarea {
  overflow: auto;
  margin: 0;
  height: 120px;
  min-height: 120px;
}

.comment-form textarea,
.comment-form input[type='text'] {
  margin: 0 4px 0 0;
  width: 300px;
}

.comment-form input[type='submit'] {
  display: block;
  float: none;
}

.post-password-form label {
  display: block;
}

.post-password-form input[type='password'] {
  margin: 0 4px 0 0;
  width: 300px;
}

.search-form input {
  float: left;
  height: 2em;
}

.search-form input[type='search'] {
  margin: 0 4px 0 0;
  width: 150px;
}

.post-password-form input {
  float: left;
  height: 2em;
}

.post-password-form input[type='password'] {
  width: 150px;
}

.comment-form input[type='submit'],
.post-password-form input[type='submit'],
.search-form input[type='submit'] {
  background: #333;
  color: #fff;
  height: 2em;
  border: none;
  padding: 0 .5em;
}

.comment-form input[type='submit']:hover,
.post-password-form input[type='submit']:hover,
.search-form input[type='submit']:hover {
  opacity: 0.8;
}

.commentlist {
  margin: 0 0 1.2em;
}

.commentlist .edit-link {
  margin: 0;
}

.commentlist .avatar-holder {
  float: left;
  margin: 0 1.2em 4px 0;
}

.commentlist-item .commentlist-item {
  padding: 0;
}

@media (min-width: 768px) {
  .commentlist-item .commentlist-item {
    padding: 0 0 0 2em;
  }
}

.comment,
.commentlist-holder {
  overflow: hidden;
}

.commentlist-item .commentlist-item,
.commentlist-item + .commentlist-item {
  padding-top: 1.2em;
}

.widget_calendar #next {
  text-align: right;
}

.gallery-item dt {
  float: none;
  padding: 0;
}

.navigation,
.navigation-comments {
  position: relative;
  vertical-align: middle;
  margin-bottom: 1.2em;
}

.navigation .screen-reader-text,
.navigation-comments .screen-reader-text {
  position: absolute;
  left: -99999px;
}

.navigation .page-numbers,
.navigation-comments .page-numbers {
  display: inline-block;
  vertical-align: middle;
  padding: 0 .4em;
}

@media (max-width: 767px) {
  .navigation .page-numbers,
  .navigation-comments .page-numbers {
    padding: 0;
  }
}

.navigation .next,
.navigation .prev,
.navigation-comments .next,
.navigation-comments .prev {
  display: inline-block;
  vertical-align: middle;
  max-width: 50%;
  background: #666;
  color: #fff;
  padding: .4em;
  text-decoration: none;
}

@media (max-width: 767px) {
  .navigation .next,
  .navigation .prev,
  .navigation-comments .next,
  .navigation-comments .prev {
    padding: .2em;
  }
}

.navigation-single {
  margin-bottom: 1.2em;
}

.navigation-single .next,
.navigation-single .prev {
  float: left;
  vertical-align: middle;
  max-width: 50%;
  background: #666;
  color: #fff;
  text-decoration: none;
}

.navigation-single .next a,
.navigation-single .prev a {
  color: inherit;
  text-decoration: none;
  padding: .4em;
  display: block;
}

@media (max-width: 767px) {
  .navigation-single .next a,
  .navigation-single .prev a {
    padding: .2em;
  }
}

.navigation-single .next {
  float: right;
}

.wp-caption {
  max-width: 100%;
  clear: both;
  border: 1px solid #ccc;
  padding: .4em;
  margin-bottom: 1.2em;
}

.wp-caption img {
  margin: 0;
  vertical-align: top;
}

.wp-caption p {
  margin: .4em 0 0;
}

div.aligncenter {
  margin: 0 auto 1.2em;
}

img.aligncenter {
  display: block;
  margin: 0 auto;
}

.alignleft {
  float: left;
  margin: 0 1.2em 4px 0;
}

.alignright {
  float: right;
  margin: 0 0 4px 1.2em;
}

.mejs-container {
  margin-bottom: 1.2em;
}

.wp-caption-text,
.gallery,
.alignnone,
.gallery-caption,
.sticky,
.bypostauthor {
  height: auto;
}

