/*------------------------------------------------------------------
	3. main headinig styles / .main-heading
-------------------------------------------------------------------*/
.main-heading {
  margin-bottom: 76px;
}

.main-heading .heading {
  color: #333;
  margin: 0 0 18px;
  font: 700 28px/30px "Ubuntu", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.main-heading p {
  margin: 0;
}

/*------------------------------------------------------------------
	4. main wrapper styles / #wrapper
-------------------------------------------------------------------*/
#wrapper {
  width: 100%;
  overflow: hidden;
  position: relative;
}

/*------------------------------------------------------------------
	5. header of the page styles / #header
-------------------------------------------------------------------*/
#header {
  z-index: 2;
  position: relative;
  padding: 34px 0 70px;
}

#header.sticky #nav {
  top: 0;
  bottom: auto;
  position: fixed;
}

#header.sticky #nav .nav-list {
  border-radius: 0 4px 4px 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

#header .nav-opener {
  width: 45px;
  height: 40px;
  display: none;
  font-size: 24px;
  margin: 4px 0 0;
  line-height: 34px;
  border-radius: 3px;
  text-align: center;
  transition: all ease 0.3s;
  border: 1px solid rgba(255, 255, 255, 0.5);
}

/*------------------------------------------------------------------
	6. navigation of the page styles / #nav
-------------------------------------------------------------------*/
#nav {
  left: 0;
  right: 0;
  bottom: -38px;
  font-weight: bold;
  position: absolute;
  transition: all ease 0.3s;
  font-family: "Ubuntu", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

#nav .nav-list {
  margin: 0;
  overflow: hidden;
  background: #fff;
  border-radius: 4px;
  padding: 18px 31px 18px 28px;
}

#nav .nav-list li {
  padding: 7px 27px 0 0;
  vertical-align: middle;
}

#nav .nav-list a {
  color: #333;
  transition: all ease 0.3s;
}

#nav .nav-list .button {
  float: right;
  padding: 0;
}

#nav .nav-list .btn {
  color: #fff;
  font-size: 12px;
  padding: 9px 15px;
}

.top-list {
  margin: 0;
  font-family: "Ubuntu", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.top-list li {
  position: relative;
  padding: 0 8.8% 0 7%;
}

.top-list .fa {
  right: 0;
  top: -3px;
  width: 50px;
  height: 50px;
  font-size: 18px;
  line-height: 50px;
  position: absolute;
  text-align: center;
  background: #1f1f1f;
  border-radius: 560%;
  border: 1px solid #292929;
}

.top-list strong {
  display: block;
  margin: 0 0 -4px;
}

.top-list a {
  color: #ccc;
}

.mt-social {
  margin: 0;
  font-size: 16px;
}

.mt-social li {
  padding: 0 0 0 5%;
}

.mt-social a {
  opacity: 0.3;
  color: #fff;
  transition: all ease 0.3s;
}

.mt-social a:hover {
  opacity: 1;
}

#main {
  width: 100%;
  overflow: hidden;
  position: relative;
}

/*------------------------------------------------------------------
	7. main banner of the page styles / .main-banner
-------------------------------------------------------------------*/
.main-banner {
  padding: 220px 0;
  overflow: hidden;
  position: relative;
}

.main-banner.small {
  padding: 180px 0;
}

.main-banner .title {
  color: #f8f8f8;
  display: block;
  margin: 0 0 4px;
}

.main-banner .heading {
  color: #fff;
  margin: 0 0 18px;
  font: 700 48px/60px "Ubuntu", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.main-banner .divider {
  margin: 0 0 41px;
  vertical-align: top;
  display: inline-block;
}

.main-banner .btn {
  padding: 18px 28px;
}

.main-banner .arrow {
  right: 40px;
  width: 185px;
  height: 103px;
  bottom: -39px;
  position: absolute;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.main-banner .promo-info {
  color: #fff;
  display: block;
  letter-spacing: 1px;
  font-family: "Ubuntu", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

.main-banner .btn-play {
  width: 60px;
  height: 60px;
  font-size: 16px;
  line-height: 60px;
  border-radius: 50%;
  background: #fff;
  text-align: center;
  padding: 0 0 0 4px;
  margin: 0 18px 0 0;
  display: inline-block;
  vertical-align: middle;
  transition: all ease 0.3s;
}

